import React, { useState, useRef, useEffect } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { Plus, Trash2 } from 'lucide-react'
import { usePlanningStore } from '../store/planningStore'
import { simplePlanningApi, SimpleSlot, SimpleSlotUpdate } from '../services/simplePlanningApi'
import SlotModal from './SlotModal'
import CompactBlockPalette from './CompactBlockPalette'
import PlanningNavigation from './PlanningNavigation'
import { getCategoryColors, getSlotStyle, getCellBackgroundStyle } from '../utils/categoryColors'

const DAYS = ['Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche']
const HOURS = Array.from({ length: 14 }, (_, i) => 7 + i) // 7h  20h

// Utilitaires de conversion
const minutesToTime = (minutes: number): string => {
  const hours = Math.floor(minutes / 60)
  const mins = minutes % 60
  return `${hours.toString().padStart(2, '0')}:${mins.toString().padStart(2, '0')}`
}

const timeToMinutes = (time: string): number => {
  const [hours, minutes] = time.split(':').map(Number)
  return hours * 60 + minutes
}

const getWeekStart = (date: Date): string => {
  const d = new Date(date)
  const day = d.getDay()
  const diff = d.getDate() - day + (day === 0 ? -6 : 1) // Lundi
  d.setDate(diff)
  return d.toISOString().split('T')[0]
}

const addDaysToDate = (dateStr: string, days: number): string => {
  const date = new Date(dateStr)
  date.setDate(date.getDate() + days)
  return date.toISOString().split('T')[0]
}

const PlanningGrid: React.FC = () => {
  const { 
    selectedEmployeeId, 
    selectedWeekKind, 
    selectedVacationPeriod,
    selectedWeekStart,
    setSelectedWeekStart 
  } = usePlanningStore()
  const queryClient = useQueryClient()
  
  const [selectedSlot, setSelectedSlot] = useState<SimpleSlot | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [newSlotData, setNewSlotData] = useState<{ date: string; dayOfWeek: number; startTime: number } | null>(null)
  
  // tats pour le scroll horizontal mobile
  const [canScrollLeft, setCanScrollLeft] = useState(false)
  const [canScrollRight, setCanScrollRight] = useState(false)
  const planningContainerRef = useRef<HTMLDivElement>(null)


  // Calculer la semaine  afficher selon le type slectionn
  const getDisplayWeekStart = () => {
    switch (selectedWeekKind) {
      case 'type':
        return '2024-01-01' // Semaine type fixe
      case 'current':
        return selectedWeekStart || getWeekStart(new Date())
      case 'next':
        const nextWeek = new Date(selectedWeekStart || getWeekStart(new Date()))
        nextWeek.setDate(nextWeek.getDate() + 7)
        return getWeekStart(nextWeek)
      default:
        return selectedWeekStart || getWeekStart(new Date())
    }
  }

  const displayWeekStart = getDisplayWeekStart()

  console.log(' PlanningGrid render - Employ:', selectedEmployeeId, 'Type:', selectedWeekKind, 'Semaine:', displayWeekStart)

  // Query pour rcuprer le planning de la semaine
  const { data: weekPlanning, isLoading, error } = useQuery({
    queryKey: ['week-planning', selectedEmployeeId, selectedWeekKind, selectedVacationPeriod, displayWeekStart],
    queryFn: async () => {
      if (!selectedEmployeeId) {
        console.log(' Pas d\'employ slectionn')
        return null
      }
      
      console.log(' Tentative de rcupration planning:', { 
        employeeId: selectedEmployeeId, 
        weekStart: displayWeekStart,
        weekKind: selectedWeekKind,
        vacationPeriod: selectedVacationPeriod
      })
      
      try {
        const result = await simplePlanningApi.getWeekPlanning(selectedEmployeeId, displayWeekStart)
        console.log(' Planning rcupr avec succs:', result)
        return result
      } catch (err) {
         console.error(' Erreur lors de la rcupration du planning:', err)
         console.error(' Dtails de l\'erreur:', {
           message: err instanceof Error ? err.message : 'Erreur inconnue',
           status: (err as any)?.status,
           url: (err as any)?.url
         })
         throw err
       }
    },
    enabled: !!selectedEmployeeId,
    staleTime: 30000,
    retry: 1
  })

  console.log(' tat de la query:', { 
    isLoading, 
    error: error?.message, 
    hasData: !!weekPlanning,
    dataLength: weekPlanning?.slots?.length 
  })

  // Mutations
  const createSlotMutation = useMutation({
    mutationFn: simplePlanningApi.createSlot,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['week-planning'] })
      setIsModalOpen(false)
      setNewSlotData(null)
      console.log(' Crneau cr avec succs')
    },
    onError: (error) => {
      console.error(' Erreur cration crneau:', error)
      alert(`Erreur lors de la cration: ${error.message}`)
    }
  })

  const updateSlotMutation = useMutation({
    mutationFn: ({ slotId, slotData }: { slotId: number; slotData: SimpleSlotUpdate }) => {
      console.log(' Dbut mutation updateSlot:', { slotId, slotData })
      return simplePlanningApi.updateSlot(slotId, slotData)
    },
    onSuccess: (data) => {
      console.log(' Crneau modifi avec succs:', data)
      queryClient.invalidateQueries({ queryKey: ['week-planning'] })
      setIsModalOpen(false)
      setSelectedSlot(null)
    },
    onError: (error) => {
      console.error(' Erreur modification crneau:', error)
      console.error(' Dtails erreur:', {
        message: error.message,
        stack: error.stack,
        name: error.name
      })
      alert(`Erreur lors de la modification: ${error.message}`)
    }
  })

  const deleteSlotMutation = useMutation({
    mutationFn: simplePlanningApi.deleteSlot,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['week-planning'] })
      console.log(' Crneau supprim avec succs')
    },
    onError: (error) => {
      console.error(' Erreur suppression crneau:', error)
      alert(`Erreur lors de la suppression: ${error.message}`)
    }
  })

  // Gestionnaires d'vnements
  const handleCellClick = (dayIndex: number, hour: number) => {
    if (!selectedEmployeeId) return
    
    const date = addDaysToDate(displayWeekStart, dayIndex)
    const startTime = hour * 60 // Convertir l'heure en minutes
    
    console.log(' Clic cellule:', { dayIndex, hour, date, startTime })
    
    setNewSlotData({ date, dayOfWeek: dayIndex, startTime })
    setSelectedSlot(null)
    setIsModalOpen(true)
  }

  // tat pour tracker le type de drag en cours
  const [dragType, setDragType] = useState<'palette' | 'existing' | null>(null)
  


  // Gestionnaires drag & drop
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    
    // Toujours autoriser le drop
    if (dragType === 'existing') {
      e.dataTransfer.dropEffect = 'move'
    } else {
      e.dataTransfer.dropEffect = 'copy'
    }
    
    // Ajouter une classe pour le feedback visuel
    const target = e.currentTarget as HTMLElement
    target.classList.add('drag-over')
  }

  const handleDragLeave = (e: React.DragEvent) => {
    const target = e.currentTarget as HTMLElement
    target.classList.remove('drag-over')
  }

  const handleDrop = (e: React.DragEvent, dayIndex: number, hour: number) => {
    e.preventDefault()
    e.stopPropagation()
    
    console.log(' Drop dtect sur:', { dayIndex, hour })
    
    // Nettoyer le feedback visuel
    const target = e.currentTarget as HTMLElement
    target.classList.remove('drag-over')
    
    if (!selectedEmployeeId) {
      console.log(' Pas d\'employ slectionn')
      return
    }
    
    try {
      const draggedData = JSON.parse(e.dataTransfer.getData('application/json'))
      const date = addDaysToDate(displayWeekStart, dayIndex)
      const startTime = hour * 60
      
      console.log(' Donnes dragges:', draggedData)
      
      if (draggedData.isExistingSlot) {
        // Dplacement d'un crneau existant - LOGIQUE QUI MARCHAIT AVANT
        const duration = draggedData.end_time - draggedData.start_time
        const endTime = startTime + duration
        
        console.log(' Dplacement crneau:', { 
          draggedData, 
          dayIndex, 
          hour, 
          date, 
          startTime, 
          endTime,
          selectedEmployeeId
        })
        
        // Solution qui marchait : crer d'abord (duplication), supprimer aprs
        console.log(' Duplication instantane puis suppression...')
        
        // D'abord crer le nouveau crneau (duplication instantane)
        // On passe l'ID du crneau qu'on dplace pour viter la dtection de chevauchement avec lui-mme
        const createData = {
          employee_id: Number(selectedEmployeeId),
          date: date,
          day_of_week: dayIndex,
          start_time: startTime,
          end_time: endTime,
          title: draggedData.title,
          category: draggedData.category,
          comment: draggedData.comment || '',
          exclude_id: draggedData.id // ID du crneau  ignorer pour le chevauchement
        }
        
        createSlotMutation.mutate(createData, {
          onSuccess: () => {
            console.log(' Nouveau crneau cr, suppression de l\'ancien...')
            // Puis supprimer l'ancien crneau
            deleteSlotMutation.mutate(draggedData.id, {
              onError: (error) => {
                console.error(' Erreur suppression ancien crneau:', error)
                // Pas d'alerte ici car le nouveau crneau est dj cr
              }
            })
          },
          onError: (error) => {
            console.error(' Erreur cration nouveau crneau:', error)
            alert('Erreur lors du dplacement du crneau')
          }
        })
      } else {
        // Cration d'un nouveau crneau depuis la palette
        const endTime = startTime + draggedData.defaultDuration
        
        console.log(' Cration crneau:', { draggedData, dayIndex, hour, date, startTime, endTime })
        
        // Crer directement le crneau
        createSlotMutation.mutate({
          employee_id: selectedEmployeeId,
          date: date,
          day_of_week: dayIndex,
          start_time: startTime,
          end_time: endTime,
          title: draggedData.title,
          category: draggedData.category,
          comment: draggedData.description || ''
        })
      }
    } catch (error) {
      console.error('Erreur lors du drop:', error)
    }
     }

  // Gestionnaires drag & drop pour les crneaux existants
  const handleSlotDragStart = (e: React.DragEvent, slot: SimpleSlot) => {
    e.stopPropagation()
    
    console.log(' Dbut drag crneau existant:', slot)
    setDragType('existing')
    
    // Stocker les donnes du crneau pour le dplacement
    const slotData = {
      ...slot,
      isExistingSlot: true
    }
    
    e.dataTransfer.setData('application/json', JSON.stringify(slotData))
    e.dataTransfer.effectAllowed = 'move'
    
    // Ajouter une classe pour l'animation
    const target = e.target as HTMLElement
    target.classList.add('dragging')
  }

  const handleSlotDragEnd = (e: React.DragEvent) => {
    console.log(' Fin drag crneau')
    setDragType(null)
    
    const target = e.target as HTMLElement
    target.classList.remove('dragging')
  }

  // TIREMENT AVEC POINTEREVENTS - ADAPT  NOTRE STRUCTURE
  const [resizingSlot, setResizingSlot] = useState<SimpleSlot | null>(null)
  const [tempStartTime, setTempStartTime] = useState<number>(0)
  const [tempEndTime, setTempEndTime] = useState<number>(0)
  const [horizontalPreviewDays, setHorizontalPreviewDays] = useState<number>(0)
  
  const draggingRef = useRef<{
    kind: "resize-start" | "resize-end" | "resize-horizontal"
    startAt: number
    endAt: number
    pointerStartY: number
    pointerStartX: number
  } | null>(null)

  // Constantes de configuration
  const PX_PER_MIN = 1.6
  const STEP_MIN = 60
  const MIN_DURATION = 60

  const snap = (m: number) => Math.round(m / STEP_MIN) * STEP_MIN
  const clamp = (v: number, a: number, b: number) => Math.max(a, Math.min(b, v))
  
  // Fonction pour obtenir l'index du jour  partir d'une date
  const getDayIndex = (dateStr: string) => {
    const slotDate = new Date(dateStr)
    const startDate = new Date(displayWeekStart)
    const diffTime = slotDate.getTime() - startDate.getTime()
    const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24))
    return diffDays
  }

  // Au clic sur la poigne
  const onPointerDown = (slot: SimpleSlot, kind: "resize-start" | "resize-end" | "resize-horizontal") => (e: React.PointerEvent) => {
    e.preventDefault()
    e.stopPropagation()
    e.currentTarget.setPointerCapture(e.pointerId)
    
    console.log(' Dbut tirement PointerEvent:', { slot, kind })
    
    setResizingSlot(slot)
    setTempStartTime(slot.start_time)
    setTempEndTime(slot.end_time)
    
    draggingRef.current = {
      kind,
      startAt: slot.start_time,
      endAt: slot.end_time,
      pointerStartY: e.clientY,
      pointerStartX: e.clientX,
    }
  }

  // Pendant le drag
  const onPointerMove = (e: React.PointerEvent) => {
    if (!draggingRef.current || !resizingSlot) return

    const { kind, startAt, endAt, pointerStartY, pointerStartX } = draggingRef.current

    if (kind === "resize-horizontal") {
      // tirement horizontal : mise  jour du preview
      const deltaX = e.clientX - pointerStartX
      const deltaDays = Math.max(0, Math.round(deltaX / 200)) // 200px par jour approximativement
      setHorizontalPreviewDays(deltaDays)
      console.log(' tirement horizontal:', { deltaX, deltaDays })
    } else {
      // tirement vertical
      const deltaMin = (e.clientY - pointerStartY) / PX_PER_MIN
      const delta = snap(deltaMin)

      if (kind === "resize-start") {
        let next = clamp(startAt + delta, 0, endAt - MIN_DURATION)
        setTempStartTime(next)
      } else {
        let next = clamp(endAt + delta, startAt + MIN_DURATION, 24 * 60)
        setTempEndTime(next)
      }
    }
  }

  //  la fin du drag
  const onPointerUp = (e: React.PointerEvent) => {
    if (!draggingRef.current || !resizingSlot) return
    
    const { kind, pointerStartX } = draggingRef.current
    
    if (kind === "resize-horizontal") {
      // tirement horizontal : crer des blocs spars sur plusieurs jours
      const deltaX = e.clientX - pointerStartX
      const deltaDays = Math.round(deltaX / 200) // 200px par jour
      
      if (deltaDays > 0) {
        const numDays = Math.min(6, deltaDays) // Maximum 6 jours supplmentaires
        
        console.log(' Cration blocs sur', numDays + 1, 'jours')
        
        // Crer des crneaux spars sur les jours suivants
        for (let i = 1; i <= numDays; i++) {
          const newDate = new Date(resizingSlot.date)
          newDate.setDate(newDate.getDate() + i)
          
          createSlotMutation.mutate({
            employee_id: Number(selectedEmployeeId),
            date: newDate.toISOString().split('T')[0],
            day_of_week: (resizingSlot.day_of_week + i) % 7,
            start_time: resizingSlot.start_time,
            end_time: resizingSlot.end_time,
            title: resizingSlot.title,
            category: resizingSlot.category,
            comment: resizingSlot.comment || ''
          })
        }
      }
    } else {
      // tirement vertical : mise  jour de la dure
      const finalStartTime = snap(tempStartTime)
      const finalEndTime = snap(tempEndTime)
      
      console.log(' Fin tirement vertical, mise  jour:', { 
        slotId: resizingSlot.id, 
        start_time: finalStartTime, 
        end_time: finalEndTime 
      })
      
      // Mise  jour via API
      updateSlotMutation.mutate({
        slotId: resizingSlot.id,
        slotData: {
          start_time: finalStartTime,
          end_time: finalEndTime
        }
      })
    }
    
    // Nettoyage
    draggingRef.current = null
    setResizingSlot(null)
    setTempStartTime(0)
    setTempEndTime(0)
    setHorizontalPreviewDays(0)
  }

     const handleSlotClick = (slot: SimpleSlot) => {
      // Empcher l'ouverture du modal pendant l'tirement
      if (resizingSlot) return
      
      console.log(' Clic crneau:', slot)
      setSelectedSlot(slot)
      setIsModalOpen(true)
    }

  const handleDeleteSlot = (slotId: number, event: React.MouseEvent) => {
    event.stopPropagation()
    deleteSlotMutation.mutate(slotId)
  }

  const handleSaveSlot = (slotData: any) => {
    if (!selectedEmployeeId) return

    if (selectedSlot) {
      // Modification
      updateSlotMutation.mutate({
        slotId: selectedSlot.id,
        slotData: {
          title: slotData.title,
          category: slotData.category,
          comment: slotData.comment,
          start_time: timeToMinutes(slotData.startTime),
          end_time: timeToMinutes(slotData.endTime)
        }
      })
    } else if (newSlotData) {
      // Cration
      createSlotMutation.mutate({
        employee_id: selectedEmployeeId,
        date: newSlotData.date,
        day_of_week: newSlotData.dayOfWeek,
        start_time: timeToMinutes(slotData.startTime),
        end_time: timeToMinutes(slotData.endTime),
        title: slotData.title,
        category: slotData.category,
        comment: slotData.comment || ''
      })
    }
  }

  // Fonction pour obtenir les crneaux d'un jour spcifique
  const getSlotsForDay = (dayIndex: number): SimpleSlot[] => {
    if (!weekPlanning?.slots) return []
    
    const dayDate = addDaysToDate(displayWeekStart, dayIndex)
    return weekPlanning.slots.filter(slot => slot.date === dayDate)
  }

  // Fonction pour vrifier si une cellule a un crneau
  const getSlotAtTime = (dayIndex: number, hour: number): SimpleSlot | null => {
    const slots = getSlotsForDay(dayIndex)
    const timeInMinutes = hour * 60
    
    return slots.find(slot => 
      slot.start_time <= timeInMinutes && slot.end_time > timeInMinutes
    ) || null
  }

  // Fonction pour vrifier si c'est la premire cellule d'un crneau
  const isSlotStart = (slot: SimpleSlot, hour: number): boolean => {
    const hourStart = hour * 60
    return slot.start_time >= hourStart && slot.start_time < hourStart + 60
  }

  // Fonction pour calculer la hauteur d'un crneau en cellules
  const getSlotHeight = (slot: SimpleSlot): number => {
    const durationInMinutes = slot.end_time - slot.start_time
    
    // Pour les crneaux de moins de 60 minutes, on ajuste visuellement
    if (durationInMinutes < 60) {
      return durationInMinutes / 60 // 0.5 pour 30min, 0.75 pour 45min
    }
    
    const durationInHours = durationInMinutes / 60
    return Math.max(1, Math.ceil(durationInHours))
  }

  // Navigation semaine
  const goToPreviousWeek = () => {
    if (selectedWeekKind !== 'type') {
      const prevWeek = new Date(displayWeekStart)
      prevWeek.setDate(prevWeek.getDate() - 7)
      setSelectedWeekStart(getWeekStart(prevWeek))
    }
  }

  const goToNextWeek = () => {
    if (selectedWeekKind !== 'type') {
      const nextWeek = new Date(displayWeekStart)
      nextWeek.setDate(nextWeek.getDate() + 7)
      setSelectedWeekStart(getWeekStart(nextWeek))
    }
  }

  const goToCurrentWeek = () => {
    setSelectedWeekStart(getWeekStart(new Date()))
  }

  // Fonctions pour le scroll horizontal
  const checkScrollButtons = () => {
    if (planningContainerRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = planningContainerRef.current
      setCanScrollLeft(scrollLeft > 0)
      setCanScrollRight(scrollLeft < scrollWidth - clientWidth - 1)
    }
  }

  const scrollLeft = () => {
    if (planningContainerRef.current) {
      planningContainerRef.current.scrollBy({ left: -200, behavior: 'smooth' })
    }
  }

  const scrollRight = () => {
    if (planningContainerRef.current) {
      planningContainerRef.current.scrollBy({ left: 200, behavior: 'smooth' })
    }
  }

  // Vrifier les boutons de scroll au montage et lors du scroll
  useEffect(() => {
    checkScrollButtons()
    const container = planningContainerRef.current
    if (container) {
      container.addEventListener('scroll', checkScrollButtons)
      window.addEventListener('resize', checkScrollButtons)
      return () => {
        container.removeEventListener('scroll', checkScrollButtons)
        window.removeEventListener('resize', checkScrollButtons)
      }
    }
  }, [weekPlanning])



  if (!selectedEmployeeId) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="text-6xl mb-4"></div>
          <h3 className="text-lg font-medium text-gray-900 mb-2">Aucun employ slectionn</h3>
          <p className="text-gray-500">Slectionnez un employ pour voir son planning</p>
        </div>
      </div>
    )
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-500">Chargement du planning...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="text-6xl mb-4"></div>
          <h3 className="text-lg font-medium text-red-900 mb-2">Erreur de chargement</h3>
          <p className="text-red-600">Impossible de charger le planning</p>
          <button 
            onClick={() => queryClient.invalidateQueries({ queryKey: ['week-planning'] })}
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            Ressayer
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-4">
      {/* Palette de blocs compacte - visible seulement sur mobile/tablette */}
      <div className="xl:hidden">
        <CompactBlockPalette />
      </div>

      {/* Navigation semaine */}
      <div className="flex items-center justify-between bg-white p-4 rounded-lg shadow">
        <button 
          onClick={goToPreviousWeek}
          className="px-4 py-2 bg-gray-100 hover:bg-gray-200 rounded"
        >
           Semaine prcdente
        </button>
        
        <div className="text-center">
          <h2 className="text-lg font-semibold">
            {selectedWeekKind === 'type' && 'Semaine type'}
            {selectedWeekKind === 'current' && `Semaine actuelle - ${new Date(displayWeekStart).toLocaleDateString('fr-FR')}`}
            {selectedWeekKind === 'next' && `Semaine suivante - ${new Date(displayWeekStart).toLocaleDateString('fr-FR')}`}
            {selectedWeekKind === 'vacation' && `Vacances ${selectedVacationPeriod} - ${new Date(displayWeekStart).toLocaleDateString('fr-FR')}`}
          </h2>
          <button 
            onClick={goToCurrentWeek}
            className="text-sm text-blue-600 hover:text-blue-800"
          >
            Aller  cette semaine
          </button>
        </div>
        
        <button 
          onClick={goToNextWeek}
          className="px-4 py-2 bg-gray-100 hover:bg-gray-200 rounded"
        >
          Semaine suivante 
        </button>
      </div>

      {/* Grille de planning */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        {/* Desktop: Grille normale */}
        <div className="hidden xl:block">
          <div className="grid grid-cols-8 gap-0">
            {/* En-tte avec les jours */}
            <div className="bg-gray-50 p-3 font-medium text-center border-b">Heures</div>
            {DAYS.map((day, index) => (
              <div key={day} className="bg-gray-50 p-3 font-medium text-center border-b">
                {day}
                <div className="text-xs text-gray-500 mt-1">
                  {new Date(addDaysToDate(displayWeekStart, index)).toLocaleDateString('fr-FR', { day: '2-digit', month: '2-digit' })}
                </div>
              </div>
            ))}
            
            {/* Lignes d'heures desktop */}
            {HOURS.map(hour => (
              <React.Fragment key={hour}>
                {/* Colonne des heures */}
                <div className="bg-gray-50 p-3 text-center font-medium border-b border-r">
                  {hour}:00
                </div>
                
                {/* Cellules pour chaque jour */}
                {DAYS.map((_, dayIndex) => {
                  const slot = getSlotAtTime(dayIndex, hour)
                  const shouldShowSlot = slot && isSlotStart(slot, hour)
                  // Utiliser les temps temporaires pendant l'tirement pour feedback visuel
                  const isBeingResized = slot && resizingSlot?.id === slot.id
                  const isHorizontalResize = isBeingResized && draggingRef.current?.kind === "resize-horizontal"
                  
                  // Preview horizontal : afficher les futurs blocs
                  const isHorizontalPreview = resizingSlot && !slot && horizontalPreviewDays > 0 && 
                    draggingRef.current?.kind === "resize-horizontal" && 
                    dayIndex > getDayIndex(resizingSlot.date) && 
                    dayIndex <= getDayIndex(resizingSlot.date) + horizontalPreviewDays &&
                    hour === resizingSlot.start_time

                  const slotHeight = slot ? getSlotHeight(slot) : 0

                  return (
                    <div
                      key={`${dayIndex}-${hour}`}
                      className={`
                        relative h-16 border-b border-r cursor-pointer transition-colors
                        ${slot ? '' : 'hover:bg-gray-50'}
                      `}
                      style={slot ? getCellBackgroundStyle(slot.category) : {}}
                      onClick={() => {
                        if (resizingSlot) return // Empcher clic pendant tirement
                        slot ? handleSlotClick(slot) : handleCellClick(dayIndex, hour)
                      }}
                      onDragOver={handleDragOver}
                      onDragLeave={handleDragLeave}
                      onDrop={(e) => handleDrop(e, dayIndex, hour)}
                    >
                      {shouldShowSlot ? (
                        <div 
                          className={`absolute inset-1 text-white rounded p-1 text-xs overflow-hidden z-10 cursor-move slot-container ${
                            isBeingResized ? (isHorizontalResize ? 'slot-resizing-horizontal' : 'slot-resizing') : ''
                          }`}
                          style={{
                            ...getSlotStyle(slot.category),
                            height: `${slotHeight * 64 - 8}px`, // 64px par cellule - 8px pour les marges
                            minHeight: slotHeight < 1 ? `${slotHeight * 64 - 8}px` : '56px'
                          }}
                          draggable={!resizingSlot}
                          onDragStart={(e) => handleSlotDragStart(e, slot)}
                          onDragEnd={handleSlotDragEnd}
                          onPointerMove={onPointerMove}
                          onPointerUp={(e) => onPointerUp(e)}
                        >
                          <div className="font-medium text-xs">
                            {slot.title}
                          </div>
                          <div className="text-xs opacity-90">
                            {minutesToTime(isBeingResized ? tempStartTime : slot.start_time)} - {minutesToTime(isBeingResized ? tempEndTime : slot.end_time)}
                            {isBeingResized && !isHorizontalResize && (
                              <span className="text-blue-200 ml-1">
                                ({Math.round((tempEndTime - tempStartTime) / 60 * 4) / 4}h)
                              </span>
                            )}
                            {isHorizontalResize && (
                              <span className="text-green-200 ml-1 animate-pulse">
                                 Plusieurs jours
                              </span>
                            )}
                          </div>
                          <div className="text-xs opacity-70 mt-1">
                            {getCategoryColors(slot.category).name}
                          </div>
                          
                          {/* Bouton de suppression */}
                          <button
                            onClick={(e) => handleDeleteSlot(slot.id, e)}
                            className="absolute bottom-1 right-1 w-6 h-6 bg-red-500 hover:bg-red-600 rounded-full flex items-center justify-center"
                            aria-label="Supprimer le crneau"
                          >
                            <Trash2 size={12} />
                          </button>
                          
                          {/* Handles d'tirement */}
                          <div 
                            className="resize-handle resize-handle-vertical resize-handle-bottom"
                            onPointerDown={onPointerDown(slot, "resize-end")}
                            onPointerMove={onPointerMove}
                            onPointerUp={(e) => onPointerUp(e)}
                            style={{ 
                              touchAction: 'none', 
                              userSelect: 'none',
                              cursor: 'ns-resize'
                            }}
                            title="Glisser pour changer la dure"
                          ></div>
                          <div 
                            className="resize-handle resize-handle-horizontal resize-handle-right"
                            onPointerDown={onPointerDown(slot, "resize-horizontal")}
                            onPointerMove={onPointerMove}
                            onPointerUp={(e) => onPointerUp(e)}
                            style={{ 
                              touchAction: 'none', 
                              userSelect: 'none',
                              cursor: 'ew-resize'
                            }}
                            title="Glisser pour taler sur plusieurs jours"
                          ></div>
                        </div>
                      ) : isHorizontalPreview ? (
                          // Preview horizontal : bloc fantme
                          <div 
                            className="absolute inset-1 rounded p-1 text-xs overflow-hidden z-5 pointer-events-none"
                            style={{
                              ...getSlotStyle(resizingSlot.category),
                              opacity: 0.5,
                              border: '2px dashed rgba(34, 197, 94, 0.8)',
                              background: `${getSlotStyle(resizingSlot.category).backgroundColor}80`, // 50% opacity
                              height: `${getSlotHeight(resizingSlot) * 64 - 8}px`,
                              minHeight: getSlotHeight(resizingSlot) < 1 ? `${getSlotHeight(resizingSlot) * 64 - 8}px` : '56px',
                              color: getSlotStyle(resizingSlot.category).color
                            }}
                          >
                            <div className="font-medium text-xs">
                              {resizingSlot.title}
                            </div>
                            <div className="text-xs opacity-90">
                              {minutesToTime(resizingSlot.start_time)} - {minutesToTime(resizingSlot.end_time)}
                            </div>
                            <div className="text-xs opacity-70 mt-1 animate-pulse text-green-600">
                              Preview
                            </div>
                          </div>
                        ) : null}
                    </div>
                  )
                })}
              </React.Fragment>
            ))}
          </div>
        </div>

        {/* Mobile/Tablette: Grille avec scroll horizontal */}
        <div className="xl:hidden">
          <div 
            ref={planningContainerRef}
            className="planning-container-mobile"
          >
            <div className="planning-grid-responsive">
              {/* En-tte avec les jours mobile */}
              <div className="bg-gray-50 p-2 font-medium text-center border-b text-sm">Heures</div>
              {DAYS.map((day, index) => (
                <div key={day} className="bg-gray-50 p-2 font-medium text-center border-b text-sm">
                  <div className="truncate">{day.slice(0, 3)}</div>
                  <div className="text-xs text-gray-500 mt-1">
                    {new Date(addDaysToDate(displayWeekStart, index)).toLocaleDateString('fr-FR', { day: '2-digit', month: '2-digit' })}
                  </div>
                </div>
              ))}
              
              {/* Lignes d'heures mobile */}
          {HOURS.map(hour => (
            <React.Fragment key={hour}>
              {/* Colonne des heures */}
              <div className="bg-gray-50 p-3 text-center font-medium border-b border-r">
                {hour}:00
              </div>
              
              {/* Cellules pour chaque jour */}
              {DAYS.map((_, dayIndex) => {
                const slot = getSlotAtTime(dayIndex, hour)
                const shouldShowSlot = slot && isSlotStart(slot, hour)
                // Utiliser les temps temporaires pendant l'tirement pour feedback visuel
                  const isBeingResized = slot && resizingSlot?.id === slot.id
                  const isHorizontalResize = isBeingResized && draggingRef.current?.kind === "resize-horizontal"
                  
                  // Preview horizontal : afficher les futurs blocs
                  const isHorizontalPreview = resizingSlot && !slot && horizontalPreviewDays > 0 && 
                    draggingRef.current?.kind === "resize-horizontal" &&
                    dayIndex > getDayIndex(resizingSlot.date) && 
                    dayIndex <= getDayIndex(resizingSlot.date) + horizontalPreviewDays &&
                    hour === resizingSlot.start_time // Seulement au dbut du slot
                  const displayStartTime = isBeingResized ? tempStartTime : (slot?.start_time || 0)
                  const displayEndTime = isBeingResized ? tempEndTime : (slot?.end_time || 0)
                  const displaySlot = slot ? { ...slot, start_time: displayStartTime, end_time: displayEndTime } : null
                  const slotHeight = displaySlot ? getSlotHeight(displaySlot) : 1
                
                return (
                  <div 
                    key={`${hour}-${dayIndex}`}
                    className={`
                      relative h-16 border-b border-r cursor-pointer transition-colors
                      ${slot ? '' : 'hover:bg-gray-50'}
                    `}
                    style={slot ? getCellBackgroundStyle(slot.category) : {}}
                    onClick={() => {
                      if (resizingSlot) return // Empcher clic pendant tirement
                      slot ? handleSlotClick(slot) : handleCellClick(dayIndex, hour)
                    }}
                    onDragOver={handleDragOver}
                    onDragLeave={handleDragLeave}
                    onDrop={(e) => handleDrop(e, dayIndex, hour)}
                  >
                    {shouldShowSlot ? (
                      <div 
                        className={`absolute inset-1 text-white rounded p-1 text-xs overflow-hidden z-10 cursor-move slot-container ${
                          isBeingResized ? (isHorizontalResize ? 'slot-resizing-horizontal' : 'slot-resizing') : ''
                        }`}
                        style={{
                          ...getSlotStyle(slot.category),
                          height: `${slotHeight * 64 - 8}px`, // 64px par cellule - 8px pour les marges
                          minHeight: slotHeight < 1 ? `${slotHeight * 64 - 8}px` : '56px'
                        }}
                        draggable={!resizingSlot}
                        onDragStart={(e) => handleSlotDragStart(e, slot)}
                        onDragEnd={handleSlotDragEnd}
                        onPointerMove={onPointerMove}
                        onPointerUp={(e) => onPointerUp(e)}
                        onClick={(e) => {
                          if (resizingSlot) {
                            e.preventDefault()
                            e.stopPropagation()
                            return
                          }
                        }}
                      >
                        <div className="font-medium truncate">{slot.title}</div>
                        <div className={`opacity-80 ${isBeingResized ? (isHorizontalResize ? 'font-bold text-green-200' : 'font-bold text-blue-200') : ''}`}>
                          {minutesToTime(displayStartTime)} - {minutesToTime(displayEndTime)}
                          {isBeingResized && !isHorizontalResize && (
                            <span className="ml-1 text-blue-100">
                              ({Math.round((displayEndTime - displayStartTime) / 60 * 10) / 10}h)
                            </span>
                          )}
                          {isHorizontalResize && (
                            <span className="ml-1 text-green-100 animate-pulse">
                               Plusieurs jours
                            </span>
                          )}
                        </div>
                        <div className="text-xs opacity-70 mt-1">
                          {getCategoryColors(slot.category).name}
                        </div>
                        
                        {/* Bouton de suppression */}
                        <button
                          onClick={(e) => handleDeleteSlot(slot.id, e)}
                          className="absolute bottom-1 right-1 w-6 h-6 bg-red-500 hover:bg-red-600 rounded-full flex items-center justify-center"
                          aria-label="Supprimer le crneau"
                        >
                          <Trash2 size={12} />
                        </button>
                        
                        {/* Handles d'tirement */}
                        <div 
                          className="resize-handle resize-handle-vertical resize-handle-bottom"
                          onPointerDown={onPointerDown(slot, "resize-end")}
                          onPointerMove={onPointerMove}
                          onPointerUp={(e) => onPointerUp(e)}
                          style={{ 
                            touchAction: 'none', 
                            userSelect: 'none',
                            cursor: 'ns-resize'
                          }}
                          title="Glisser pour changer la dure"
                        ></div>
                        <div 
                          className="resize-handle resize-handle-horizontal resize-handle-right"
                          onPointerDown={onPointerDown(slot, "resize-horizontal")}
                          onPointerMove={onPointerMove}
                          onPointerUp={(e) => onPointerUp(e)}
                          style={{ 
                            touchAction: 'none', 
                            userSelect: 'none',
                            cursor: 'ew-resize'
                          }}
                          title="Glisser pour taler sur plusieurs jours"
                        ></div>
                      </div>
                    ) : isHorizontalPreview ? (
                        // Preview horizontal : bloc fantme
                        <div 
                          className="absolute inset-1 rounded p-1 text-xs overflow-hidden z-5 pointer-events-none"
                          style={{
                            ...getSlotStyle(resizingSlot.category),
                            opacity: 0.5,
                            border: '2px dashed rgba(34, 197, 94, 0.8)',
                            background: `${getSlotStyle(resizingSlot.category).backgroundColor}80`, // 50% opacity
                            height: `${getSlotHeight(resizingSlot) * 64 - 8}px`,
                          }}
                        >
                          <div className="font-medium truncate text-green-100">{resizingSlot.title}</div>
                          <div className="text-xs opacity-75 text-green-200">
                            {minutesToTime(resizingSlot.start_time)} - {minutesToTime(resizingSlot.end_time)}
                          </div>
                          <div className="text-xs opacity-75 text-green-200 animate-pulse">
                            Preview
                          </div>
                        </div>
                      ) : !slot ? (
                      <div className="absolute inset-0 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity">
                        <Plus size={16} className="text-gray-400" />
                      </div>
                    ) : null}
                  </div>
                )
              })}
            </React.Fragment>
          ))}
            </div>
          </div>
        </div>
      </div>

      {/* Navigation horizontale pour mobile */}
      <PlanningNavigation
        onScrollLeft={scrollLeft}
        onScrollRight={scrollRight}
        canScrollLeft={canScrollLeft}
        canScrollRight={canScrollRight}
      />

      {/* Modal */}
      {isModalOpen && (
        <SlotModal
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false)
            setSelectedSlot(null)
            setNewSlotData(null)
          }}
          onSave={handleSaveSlot}
          slot={selectedSlot}
          defaultStartTime={newSlotData ? minutesToTime(newSlotData.startTime) : undefined}
          isLoading={createSlotMutation.isPending || updateSlotMutation.isPending}
        />
      )}
    </div>
  )
}

export default PlanningGrid